const styles = {
  Container: {
    margin: '30px auto',
    boxSizing: 'border-box',
    minWidth: '321px',
    width: '90%',
    backgroundColor: '#fff',
    zIndex: '0',
    borderRadius: '24px',
    boxShadow: `0px 0px 20px rgba(0, 0, 0, 0.25)`,
    padding: '30px 25px',
    height: '90vh',
    overflow: 'auto',
  },
  parrafo: {
    margin: '40px auto',
    textAlign: 'center',
  },
}
